export default {
  name: 'vModelChild',
  props: {
    value: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      isClick: false
    }
  },

  methods: {
    onChildClick(value) {
      console.log(value, 'input事件触发了')
      this.$emit('input', value)
    }
  },
  watch: {
    value(newValue) {
      console.log(newValue, '子组件的value改变了')
    }
  }
}



