import child from './component/index'
export default {
  name: 'vModel',
  components: {
    child
  },
  data() {
    return {
      name: '原始值'
    }
  },

  methods: {
    onValueClick() {
      this.name = '改变'
    }
  },
  watch: {
    name(newValue) {
      console.log(newValue, '子组件改变了父组件的name')
    }
  }
}



